import {Container, Row, Col, } from "react-bootstrap"
import perfil from "../Archivos/Imagenes/about.jpeg"
import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
export const About = () => {
    useEffect(() => {
        Aos.init({duration: 2000});   
       }, []);
    return(
        <Container data-aos="fade" id="acerca" className="aboutme">
            <Row className='aboutrow'>
                <Col className="aboutimg"  xs={12} md={6} xl={5}>
                <img className="fotoperfil" src={perfil}></img>
                </Col>
                <Col className="abouttext" xs={12} md={6} xl={7}>
                <p className="texteo">"Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum."</p>
                </Col>
            </Row>
        </Container>
    )
}