import Nav from 'react-bootstrap/Nav';

const NavBar = () => {
   
  return (
    <Nav  fill variant="tabs" defaultActiveKey="corporativo">
      <Nav.Item>
        <Nav.Link className='active' id="corporativo">Corporativo</Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link className='active' id="eventos">Eventos</Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link className='active' id="estudio">Fotos de estudio</Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link className='active' id="outdoor">Outdoors</Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link className='active' id="moda">Portfolio de moda</Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link className='active' id="viajes">Viajes</Nav.Link>
      </Nav.Item>
    </Nav>
  );
}

export default NavBar;