import {Container, Row, Col, } from "react-bootstrap"
import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
export const Banner = () => {
    useEffect(() => {
        Aos.init({duration: 2000});   
       }, []);
    return(
        <Container data-aos="fade-down" id="inicio" className="banner">
            <Row className='bannerrow'>
                <Col xs={12} md={6} xl={5}>
                <h1>CAMILA</h1>
                <h1 className="apellido">ORAZI</h1>
                </Col>
                <Col className="bannertext" xs={12} md={6} xl={7}>
                <ul>
                    <li><a href="#acerca">SOBRE MI</a></li>
                    <li><a href="#trabajos">MIS TRABAJOS</a></li>
                    <li><a href="#contacto">CONTACTAME</a></li>
                </ul>
                </Col>
            </Row>
        </Container>
    )
}

