import {Container, Row, Col} from "react-bootstrap"
import fotocontacto from "../Archivos/Imagenes/viajes.jpeg"
import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
export const Contacto = () => {
    useEffect(() => {
        Aos.init({duration: 2000});   
       }, []);
    return(
        <Container id="contacto" data-aos="fade" className="contactocont">
            <Row className="contactorow">
                <Col className="fotocontacto"><img src={fotocontacto}></img></Col>
                <Col className="redes">
                <h2 className="contactoh2">CONTACTAME</h2>
                <ul className="iconosredes">
                <a href=""><i class="fa-brands fa-instagram"></i></a>
                <a href=""><i class="fa-regular fa-envelope"></i></a>
                <a href=""><i class="fa-brands fa-whatsapp"></i></a>
                </ul>    
                </Col>
            </Row>
        </Container>
    )
}