import foto1 from "../Archivos/Imagenes/bannerfondo.jpg"
import Aos from "aos";
import "aos/dist/aos.css";
import { useEffect } from "react";
const Corporativo = () => {
    useEffect(() => {
        Aos.init({duration: 2000});   
       }, []);
    return (
        <div data-aos="fade-down" class="grid-container">
      <div class="grid-item tall"><img src={foto1}></img></div>
      <div class="grid-item tall"><img src={foto1}></img></div>
      <div class="grid-item tall"><img src={foto1}></img></div>
      <div class="grid-item tall"><img src={foto1}></img></div>
      <div class="grid-item tall"><img src={foto1}></img></div>
      <div class="grid-item tall"><img src={foto1}></img></div>
      <div class="grid-item tall"><img src={foto1}></img></div>
      <div class="grid-item tall"><img src={foto1}></img></div>
      <div class="grid-item tall"><img src={foto1}></img></div>
      <div class="grid-item tall"><img src={foto1}></img></div>
      <div class="grid-item tall"><img src={foto1}></img></div>
 
    </div>
    )
}

export default Corporativo;